 @keyframes moveInLeft {
            0% {
                opacity: 0;
                transform: translateX(-100px)
            }

            80% {
                opacity: .50;
                transform: translateX(20px)
            }

            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }

        @keyframes moveInRight {
            0% {
                opacity: 0;
                transform: translateX(100px)
            }

            80% {
                opacity: .50;
                transform: translateX(-20px)
            }

            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }

        @keyframes moveInBottom {
            0% {
                opacity: 0;
                transform: translateY(50px)
            }

            80% {
                opacity: .50;
            }

            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }